@import url('https://fonts.googleapis.com/css2?family=Lugrasimo&family=Roboto:ital,wght@1,100&display=swap'); 
*{
  margin: 0;
  padding: 0;
  font-family: sans-serif ;
 
}  

$bg: rgb(30,30,30);

.header{
       height:8vmax;
       background-color:$bg; 
       display: flex;
       align-items: center;
       padding: 2rem;
       box-sizing: border-box;
       justify-content: space-between;
       >img{
            height: 14vmax;
       }
>svg{
     color: white;font-size: 2rem;
     cursor: pointer;

     &:hover{
      color: rgb(255, 180, 180);
     }
}
div{
     width: 100%;
     margin: 1vmax;
     >a{
      margin: 1vmax;
      font-size: 1.3rem;
      font-weight: 300;
    }
}

}
a{
  color: white;
  text-decoration: none;
}